import React from 'react';
import { Card, Typography, Grid, Box, Button } from '@mui/material';
import { LineChart } from '@mui/x-charts/LineChart';

export default function MarketingCampaigns() {
  // Example mock data for campaigns
  const campaignsData = [
    { label: 'Campaign 1', value: 75 },
    { label: 'Campaign 2', value: 60 },
    { label: 'Campaign 3', value: 80 },
  ];

  const graphData = [
    { x: '2024-01-01', y: 200 },
    { x: '2024-01-02', y: 300 },
    { x: '2024-01-03', y: 150 },
    { x: '2024-01-04', y: 400 },
    { x: '2024-01-05', y: 500 },
  ];

  return (
    <section className="bg-white rounded-lg shadow p-6 mb-6">
      <h2 className="text-lg font-semibold mb-4">Marketing Campaigns</h2>
      <p className="mb-4">Track your ongoing and past campaigns here. Below are the details of your current campaigns and a graphical overview.</p>
      
      {/* Campaigns Statistics */}
      <Grid container spacing={4} className="mb-6">
        {campaignsData.map((campaign, index) => (
          <Grid item xs={12} sm={4} key={index}>
            <Card sx={{ borderRadius: 3, boxShadow: 3, p: 3 }}>
              <Typography variant="h6" sx={{ fontWeight: 600 }} gutterBottom>
                {campaign.label}
              </Typography>
              <Typography variant="body1">Success Rate: {campaign.value}%</Typography>
            </Card>
          </Grid>
        ))}
      </Grid>

      {/* Campaign Graph */}
      <Card sx={{ borderRadius: 3, boxShadow: 3, p: 4, mb: 4 }}>
        <Typography variant="h6" sx={{ fontWeight: 600, mb: 2 }}>
          Campaign Performance Over Time
        </Typography>
        <LineChart
          xAxis={[{ data: graphData.map((d) => d.x) }]}
          series={[{ data: graphData.map((d) => d.y), color: '#4caf50' }]}
          width={500}
          height={300}
        />
      </Card>

      {/* Action Button */}
      <Box sx={{ textAlign: 'center' }}>
        <Button variant="contained" color="primary" sx={{ borderRadius: 2 }}>
          Add New Campaign
        </Button>
      </Box>
    </section>
  );
}
