import React from 'react';
import { Box, Typography, Link, Stack } from '@mui/material';

export default function AdminDashboardFooter() {
  return (
    <Box
      component="footer"
      sx={{
        backgroundColor: '#1E293B',
        color: '#FFFFFF',
        padding: '16px',
        marginTop: 'auto',
        borderTop: '4px solid #3B82F6',
        textAlign: 'center',
      }}
    >
      {/* About Section */}
      <Typography variant="body2" sx={{ marginBottom: '8px', color: '#D1D5DB' }}>
        We provide seamless admin management for your e-commerce platform.
      </Typography>

      {/* Quick Links Section */}
      <Stack direction="row" spacing={2} justifyContent="center" sx={{ marginBottom: '8px' }}>
        <Link href="/dashboard" underline="hover" sx={{ color: '#3B82F6', fontSize: '14px' }}>
          Dashboard
        </Link>
        <Link href="/products" underline="hover" sx={{ color: '#3B82F6', fontSize: '14px' }}>
          Products
        </Link>
        <Link href="/orders" underline="hover" sx={{ color: '#3B82F6', fontSize: '14px' }}>
          Orders
        </Link>
        <Link href="/settings" underline="hover" sx={{ color: '#3B82F6', fontSize: '14px' }}>
          Settings
        </Link>
      </Stack>

      {/* Footer Note */}
      <Typography variant="body2" sx={{ color: '#9CA3AF' }}>
        &copy; {new Date().getFullYear()} E-commerce Admin. All rights reserved.
      </Typography>
    </Box>
  );
}
